import { ChangeDetectionStrategy, Input, Component, HostBinding } from '@angular/core';
import { PopoverConfig } from './popover.config';
import { isBs3 } from '../utils/ng2-bootstrap-config';


export class PopoverContainerComponent {
   show = '!isBs3';
   role = 'tooltip';
   class = '"popover-fadeIn popover in popover-" + placement + " " + placement';

   public placement: string;
   public title: string;

  public get isBs3(): boolean {
    return isBs3();
  }

  public constructor(config: PopoverConfig) {
    Object.assign(this, config);
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'mdb-popover-container',
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
  <div class="popover-arrow arrow"></div>
  <h3 class="popover-header" *ngIf="title">{{title}}</h3>
  <div class="popover-body">
    <ng-content></ng-content>
  </div>`
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: PopoverConfig, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'show': [{ type: HostBinding, args: ['class.show', ] },],
'role': [{ type: HostBinding, args: ['attr.role', ] },],
'class': [{ type: HostBinding, args: ['class', ] },],
'placement': [{ type: Input },],
'title': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
